@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,500;1,800&display=swap');

:root {
  --font-base: 'Raleway', sans-serif;
  --red-color: #f26c4f;
  --dark-color: #464646;
  --light-grey: #f4f4f4;
  --white-color: #fff;
  --transition: all 300ms ease-in-out;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
html {
  font-size: 10px;
}
body {
  font-size: 1.8rem;
  line-height: 1.6;
  font-family: var(--font-base);
  color: var(--dark-color);
}
a {
  color: unset;
  text-decoration: none;
}
ul {
  list-style-type: none;
}
img {
  width: 100%;
  display: block;
}
button {
  cursor: pointer;
  outline: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  font-size: 1.8rem;
}
input, textarea {
  outline: 0;
  border: none;
  resize: none;
}
h1, h2, h3, h4 {
  line-height: 1.5;
  letter-spacing: 1px;
}